<style>
.module-item{
	/*height: 100px;*/
	width: 100%;
	background-color: #fff;
	margin-top: 20px;
    font-size: 14px;
}
.module-item img{
    margin: 10px;
}
.btn{
    height:30px;
    padding: 0 5px;
    line-height: 30px;
    margin-right: 5px;
    cursor: pointer;
    display:inline-block;
}
.browse{
    background-color:#E1E4E6; 
}
.control{
    background-color:#00b4cf; 
    color:#fff;
}
.control:hover{
    background-color:#01869a; 
}
.control-cancel{
    background-color:#435466; 
    color:#fff;
}
.control-cancel:hover{
    background-color:#2d4054; 
}
.idle{
    background-color: #7fbc39;
    padding: 5px;
    color:#fff;
}
.building{
    background-color: #ff9a2a;
    padding: 5px;
    color:#fff;
}
</style>
<template>
	<div class="module-item">
        <Row>
            <Col span="3" :style="{textAlign:'center'}"><img :src="img_src"></Col>
            <Col span="21">
                <div :style="{marginTop:'10px',fontWeight:'bold'}">
                    <Row>
                        <Col span="8">
                            <span><Icon type="ios-desktop-outline" size="20"/>  {{currentData.name}}</span>
                        </Col>
                        <Col span="4">
                            <span :class="currentData.status"> {{currentData.status}} </span>
                        </Col>
                        <Col span="6">
                            <span><Icon type="ios-information-circle-outline" size="20" /> {{currentData.ip}}</span>
                        </Col>
                        <Col span="6">
                            <span><Icon type="md-folder-open" size="20" /> {{currentData.location}}</span>
                        </Col>

                    </Row>
                </div>
                <div :style="{marginTop:'30px'}">
                    <Poptip
                        title="Separate multiple resource name with commas"
                        v-model="visible"
                        @on-popper-hide="clearValue"
                        placement="bottom-start">
                        <div slot="content">
                            <div style="margin-bottom:10px"><Input v-model="value" placeholder="e.g. Chrome,Firefox" /></div>
                            <div>
                                <span class="btn control" @click="addHandle">Add Resources</span>
                                <span class="btn control-cancel" @click="close">Cancel</span>
                            </div>
                        </div>
                        <span class="btn control" style="font-size:18px;">+</span>
                    </Poptip>
                    <span class="btn browse" v-for="(v,k) in currentData.resources" @click="removeHandle(k)"> {{v}} <Icon type="ios-trash" size="16" /></span>
                    <span style="float:right;margin-right:20px;" class="btn control"><Icon type="ios-close-circle-outline" size="16"/> Deny</span>
                </div>
            </Col>
        </Row>
    </div>
</template>
<script>
    import windowspic from '@/assets/icons/windows.png'
    import centospic from '@/assets/icons/cent_os.png'
    import debianpic from '@/assets/icons/debin.png'
    import susepic from '@/assets/icons/suse.png'
    import ubuntupic from '@/assets/icons/ubuntu.png'
	export default{
		data(){
			return{
                currentData:this.data,
                img_src:'',
                visible: false,
                value:''
			}
		},
        props:{
            data:{
                type:Object,
                default:{}
            }
        },
        methods:{
            initData:function(){
                switch(this.currentData.os){
                    case 'windows':
                        this.img_src=windowspic
                        break;
                    case 'ubuntu':
                        this.img_src=ubuntupic
                        break;
                    case 'debian':
                        this.img_src=debianpic
                        break;
                    case 'suse':
                        this.img_src=susepic
                        break;
                    case 'centos':
                        this.img_src=centospic
                        break;
                    default:
                        this.img_src=windowspic

                };
            },
            close:function(){
                this.visible = false;
            },
            clearValue:function(){
                this.value = '';
            },
            removeHandle:function(index){
                this.currentData.resources.splice(index,1);
            },
            addHandle:function(){
                var _this=this;
                if(this.value.trim()=='') return;
                var arr=this.value.trim().split(',');
                arr.forEach(function(v){
                    if(v.trim()=='') return;
                    var res=_this.currentData.resources.find(function(ele){
                        return ele.toUpperCase()==v.toUpperCase()
                    });
                    console.log('res',res)
                    if(res==undefined){
                        _this.currentData.resources.push(v);
                    }else{
                        _this.$Message.error('资源中已包含！');
                    }
                });
                this.close();
            }
        },
        mounted:function(){
            this.initData();
        }
	}
</script>


