<style scoped>
body{
    padding: 0;
    margin:0;
    color: #2d4054;
    font-family:Avenir,Helvetica,Arial,sans-serif;
    font-size: 14px;
}
.layout{
    border: 1px solid #d7dde4;
    /*background: #f5f7f9;*/
    position: relative;
    border-radius: 4px;
    /*overflow: hidden;*/
    height: -webkit-fill-available;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
.header{
    padding: 10px;
    background-color: #fff;
    text-align: center;
}
.header img{
   /* padding: 15px;*/
    height: 40px;
}
#content-bar{
    background-color: #fff;
    height:50px;
    width: 100%;
    margin: 10px 0;

}
.status{
    height:120px;
}
.status .title{
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    padding: 10px;
}
.status .num{
    color: #fff;
    font-size: 48px;
    float: right;
    padding-right: 20px;
}
.build{
    background-color: #ff9a2a;
}
.idle{
    background-color: #7fbc39;
}
.count{
    background-color: #fff;
    text-align: center;
}
.count .countTitle{ 
    padding:20px 0;
}
.count .countNum{
    font-size: 20px;
    font-weight: bold;
}
.search{
    
}
.ivu-menu-horizontal {
    height: 50px;
    line-height: 50px;
}
.ivu-menu-horizontal.ivu-menu-light:after{
    background-color: #fff;
}
.ivu-menu-light.ivu-menu-horizontal .ivu-menu-item-active, .ivu-menu-light.ivu-menu-horizontal .ivu-menu-item:hover, .ivu-menu-light.ivu-menu-horizontal .ivu-menu-submenu-active, .ivu-menu-light.ivu-menu-horizontal .ivu-menu-submenu:hover{
    border-bottom: 3px solid #2d8cf0;
}
.ivu-menu-item {
    border-right:1px solid #E1E4E6;
}
.side-history ul  li a{
    font-size: 12px;
    color: #999;
}
.side-history ul  li a:hover{
    color: #00B4CF;
}
</style>
<template>
    <div class="layout">
        <Layout>
            <Header class="header">
                <span><img src="./assets/logo/logo.svg"></span>
                <span style="float:right;padding-right:8%"><Avatar icon="ios-person" /></span>
            </Header>
            <Layout :style="{margin:'0 5%'}">
                <Sider hide-trigger :style="{background: '#2d4054',margin:'0 10px',height: '-webkit-fill-available'}">
                    <Menu active-name="1-2" theme="dark" width="auto" >
                        <MenuItem name="1-1">
                            <Icon type="ios-navigate" size=20></Icon>
                            <span>DASHBOARD</span>
                        </MenuItem>
                        <MenuItem name="1-2">
                            <Icon type="ios-search" size=20></Icon>
                            <span>AGENT</span>
                        </MenuItem>
                        <MenuItem name="1-3">
                            <Icon type="ios-settings" size=20></Icon>
                            <span>MY CRUISE</span>
                        </MenuItem>
                        <MenuItem name="1-4">
                            <Icon type="md-help-buoy" size=20></Icon>
                            <span>HELP</span>
                        </MenuItem>
                    </Menu>
                    <div class="side-history" style="position: absolute;
    bottom: 81px;">
                        <div style="color:#ccc;font-size:24px;padding:5px 15px">
                            <span>History</span>
                        </div>
                        <div>
                            <ul style="padding-left:30px">
                                <li><a>stdmngbgr02/Acceptance_test</a></li>
                                <li><a>stdmngbgr03/Acceptance_test</a></li>
                                <li><a>stdmngbgr04/Acceptance_test</a></li>
                                <li><a>stdmngbgr05/Acceptance_test</a></li>
                                <li><a>stdmngbgr06/Acceptance_test</a></li>
                                <li><a>stdmngbgr07/Acceptance_test</a></li>
                                <li><a>stdmngbgr08/Acceptance_test</a></li>
                                <li><a>stdmngbgr09/Acceptance_test</a></li>
                                <li><a>stdmngbgr10/Acceptance_test</a></li>
                                <li><a>stdmngbgr11/Acceptance_test</a></li>
                                <li><a>stdmngbgr12/Acceptance_test</a></li>
                            </ul>
                        </div>
                    </div>
                </Sider>
                <Layout>
                    <Content :style="{margin:'20px 0',padding: '0 24px', minHeight: '280px'}">
                        <div id="content-category">
                            <Row :gutter="30">
                                <Col span="8">
                                    <div class="status build">
                                        <p class="title">Building</p>
                                        <p class="num">{{buildingTotal}}</p>
                                    </div>
                                </Col>
                                <Col span="8">
                                    <div class="status idle">
                                        <p class="title">Idle</p>
                                        <p class="num">{{idleTotal}}</p>
                                    </div>
                                </Col>
                                <Col span="8">
                                    <div class="status count">
                                        <Row :gutter="16">
                                            <Col span="8">
                                                <div>
                                                    <p class="countTitle">All</p>
                                                    <p class="countNum">{{allTotal}}</p>
                                                </div>
                                            </Col>
                                            <Col span="8">
                                                <div>
                                                    <p class="countTitle">PHYSICAL</p>
                                                    <p class="countNum">{{physicalTotal}}</p>
                                                </div>
                                            </Col>
                                            <Col span="8">
                                                <div>
                                                    <p class="countTitle">VIRTUAL</p>
                                                    <p class="countNum">{{virtualTotal}}</p>
                                                </div>
                                            </Col>
                                        </Row>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        <div id="content-bar">
                            <Row>
                                <Col span="6">
                                    <Menu mode="horizontal"  active-name="1" @on-select="onMenuHandle">
                                        <MenuItem name="1">
                                            All
                                        </MenuItem>
                                        <MenuItem name="2">
                                            Physical
                                        </MenuItem>
                                        <MenuItem name="3">
                                            Virtual
                                        </MenuItem>
                                    </Menu>
                                </Col>
                                <Col span="8">
                                    <div class="search">
                                        <Input prefix="ios-search" placeholder="Enter name" style="width: auto;margin:9px" />
                                    </div>
                                </Col>
                                <Col span="10">
                                    <div :style="{float:'right',padding:'10px'}">
                                        <span style="padding-right:10px;"> <Icon type="ios-apps" size="30"/></span>
                                        <span style="padding-right:10px;"> <Icon type="md-menu" size="30"/></span>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        <div  v-show="index==1">
                            <showItem v-for="item in lists" :data='item'></showItem>
                        </div>
                        <div  v-show="index==2">
                            <showItem v-for="item in phyList" :data='item'></showItem>
                        </div>
                        <div  v-show="index==3">
                            <showItem v-for="item in virList" :data='item'></showItem>
                        </div>
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
</template>
<script>
    // import showItem  from './components/ShowItem'
    import showItem from './components/ShowItem'
    export default {
        methods:{
            onMenuHandle:function(evt){
                this.index=evt;
                console.log('index',this.index);
            },
            getData:function(){
                var _this=this;
                this.$axios({
                  method:'get',
                  url:'http://localhost:3000/agents'
                })
                  .then(function(res) {
                  _this.lists=res.data;
                  _this.initData();
                });
            },
            initData:function(){
                this.allTotal=this.lists.length;
                var _this=this;
                this.lists.forEach(function(v,k){
                    if(v.status=='building'){
                        _this.buildingTotal++;
                    }else if(v.status=='idle'){
                        _this.idleTotal++;
                    };
                    if(v.type=='physical'){
                        _this.physicalTotal++;
                        _this.phyList.push(v);
                        console.log('phylist--',v.id);
                    }else if(v.type=='virtual'){
                        _this.virtualTotal++;
                        _this.virList.push(v);
                        console.log('virList--',v.id);
                    }
                });
            }

        },
        data:function(){
            return{
                lists:[],
                index:1,
                showList:[],
                phyList:[],
                virList:[],
                buildingTotal:0,
                idleTotal:0,
                allTotal:0,
                physicalTotal:0,
                virtualTotal:0
            }
        },
        components:{
            showItem
        },
        mounted:function(){
            this.getData()
        },
        watch:{
            showList:function(){
                console.log('watch')
            }
        }
    }
</script>


